import React from 'react'
import { useState } from 'react'
import { Sidebar, Menu, MenuItem } from 'react-pro-sidebar';
import { Box, IconButton, Typography,useTheme } from '@mui/material';
import { Link } from 'react-router-dom';
import { tokens } from '../../themes';
import HomeOutlinedIcon from '@mui/icons-material/HomeOutlined';
import PeopleOutlinedIcon from '@mui/icons-material/PeopleOutlined';
import ContactsOutlinedIcon from '@mui/icons-material/ContactsOutlined';
import ReceiptOutlinedIcon from '@mui/icons-material/ReceiptOutlined';
import PersonOutlinedIcon from '@mui/icons-material/PersonOutlined';
import CalendarTodayOutlinedIcon from '@mui/icons-material/CalendarTodayOutlined';
import HelpOutlinedIcon from '@mui/icons-material/HelpOutlined';
import BarChartOutlinedIcon from '@mui/icons-material/BarChartOutlined';
import PieChartOutlineOutlinedIcon from '@mui/icons-material/PieChartOutlineOutlined';
import TimelineOutlinedIcon from '@mui/icons-material/TimelineOutlined';
import MenuOutlinedIcon from '@mui/icons-material/MenuOutlined';
import MapOutlinedIcon from '@mui/icons-material/MapOutlined';
import userImg from '../../assets/user.png'

const Item =({ title, to, icon, selected, setSelected}) =>{
  const theme=useTheme();
  const colors = tokens(theme.palette.mode);
  return (
    <Link to={to} style={{ textDecoration: "none" }}>
    <MenuItem
    active={selected === title}
    style={{color: colors.grey[100]}}
    onClick={()=>setSelected(title)}
    icon={icon}
    >
    <Typography>{title}</Typography>
    </MenuItem>
    </Link>
  )
}


const Navbar = () => {
  const theme = useTheme();
  const colors = tokens(theme.palette.mode);
  const [isCollapsed, setIsCollapsed] = useState(false);
  const[selected, setSelected] = useState("Dashboard");
  return (
    <Box
    sx={{
      "& .ps-sidebar-root": {
          background: `${colors.primary[400]} !important`,
          border:"none",
          
      },
      "& .ps-sidebar-container": {
          background: `${theme.palette.mode === "dark" ? colors.primary[400] : colors.grey[900]} !important`,
          height: "100vh !important",
          
      },
      "& .ps-menu-button": {
      },
      "& .ps-menu-button:hover":{
          color: "#868dfb !important",
          background:"none !important",
      },
      "& .ps-menu-button.active":{
        color: "#868dfb !important",
    },
  }}
    >
      <Sidebar collapsed={isCollapsed}>
        <Menu iconShape="square">
         {/* Logo And Menu Icons */}
          <MenuItem
          onClick={()=>setIsCollapsed(!isCollapsed)}
          icon={isCollapsed ? <MenuOutlinedIcon /> :undefined}
          style={{
            margin: "10px 0 20px 0",
            color: colors.grey[100],
          }}
          >
            {!isCollapsed && (
              <Box 
              display="flex"
              justifyContent="space-between"
              alignItems="center"
              ml="15px"
              >
              <Typography variant='h3' color={colors.grey[100]}>
                DASHBOARD
              </Typography>
              <IconButton
              onClick={()=> setIsCollapsed(!isCollapsed)}
              >
                <MenuOutlinedIcon />
              </IconButton>
              </Box>
            )}
          </MenuItem>
          {!isCollapsed &&(
            <Box mb="25px">
              <Box display="flex" justifyContent="center" alignItems="center">
                <img src={userImg}
                 alt="profile" width="100x" height="100px" style={{
                  cursor:"pointer",
                  borderRadius:"50%"
                 }} />
              </Box>
              <Box textAlign="center" >
                <Typography variant='h2' color={colors.grey[100]} fontWeight="bold"
                sx={{
                  m:"10px 0 0 0"
                }}> 
                  Guest User
                </Typography>
                <Typography variant='h5' color={colors.greenAccent[500]} >
                  VP Fancy Admin

                </Typography>
              </Box>
            </Box>
          )}

          <Box paddingLeft={isCollapsed ? undefined : "10%"}>
              <Item 
              title="Dashboard" to="/react-admin" icon={<HomeOutlinedIcon/>}  selected={selected} setSelected={setSelected}
              />
              <Typography
              variant='h6'
              color={colors.grey[300]}
              sx={{m: "15px 0 5px 20px"}}
              >
              Data
              </Typography>
              <Item 
              title="Manage Team" to="/team" icon={<PeopleOutlinedIcon/>}  selected={selected} setSelected={setSelected}
              />
              <Item 
              title="Contacts Info" to="/contacts" icon={<ContactsOutlinedIcon/>}  selected={selected} setSelected={setSelected}
              />
              <Item 
              title="Invoices Balances" to="/invoices" icon={<ReceiptOutlinedIcon/>}  selected={selected} setSelected={setSelected}
              />
              <Typography
              variant='h6'
              color={colors.grey[300]}
              sx={{m: "15px 0 5px 20px"}}
              >
              Pages
              </Typography>
              <Item 
              title="Profile Form" to="/form" icon={<PersonOutlinedIcon/>}  selected={selected} setSelected={setSelected}
              />
              <Item 
              title="Calendar" to="/calendar" icon={<CalendarTodayOutlinedIcon/>}  selected={selected} setSelected={setSelected}
              />
              <Item 
              title="FAQ Page" to="/faq" icon={<HelpOutlinedIcon/>}  selected={selected} setSelected={setSelected}
              />
              <Typography
              variant='h6'
              color={colors.grey[300]}
              sx={{m: "15px 0 5px 20px"}}
              >
              Charts
              </Typography>
              <Item 
              title="Bar Chart" to="/bar" icon={<BarChartOutlinedIcon/>}  selected={selected} setSelected={setSelected}
              />
              <Item 
              title="Pie Chart" to="/pie" icon={<PieChartOutlineOutlinedIcon/>}  selected={selected} setSelected={setSelected}
              />
              <Item 
              title="Line Chart" to="/line" icon={<TimelineOutlinedIcon/>}  selected={selected} setSelected={setSelected}
              />
              <Item 
              title="Geography Chart" to="/geography" icon={<MapOutlinedIcon/>}  selected={selected} setSelected={setSelected}
              />
          </Box>
        </Menu>
      </Sidebar>
    </Box>
  )
}

export default Navbar
